/* Basic Website Layout
   These styles control the overall look and feel of the website.
   They ensure text is easy to read and content is properly spaced. */
body {
  font-family: Arial, sans-serif;
  /* Uses a clean, readable font */
  margin: 0;
  /* Removes default spacing around the page */
  padding: 20px;
  /* Adds comfortable spacing inside the page */
  line-height: 1.6;
  /* Makes text easier to read */
}

/* Content Container
   This keeps the website content from getting too wide on large screens,
   making it easier to read and navigate. */
.container {
  max-width: 800px;
  /* Limits how wide the content can get */
  margin: 0 auto;
  /* Centers the content on the page */
}

/* Navigation Bar Styles
   These styles control how the navigation menu looks at the top of each page.
   They make it easy for users to see and use the navigation links. */
.nav {
  background-color: #f8f9fa;
  padding: 1rem;
  margin-bottom: 2rem;
}

/* Navigation Links
   These styles make the navigation links look clickable and highlight
   when users hover over them or when they're on the current page. */
.nav a {
  margin-right: 1rem;
  /* Spaces out the navigation links */
  text-decoration: none;
  /* Removes the underline from links */
  color: #333;
  /* Makes links dark and readable */
}

.nav a:hover {
  color: #007bff;
  /* Makes links blue when hovered over */
}

.nav a.active {
  color: #007bff;
  /* Shows which page the user is currently on */
  font-weight: bold;
  text-decoration: underline;
}

/* Cookie Settings Page Styles
   These styles control how the cookie preferences page looks.
   They make it clear and easy for users to manage their cookie settings. */
.cookie-section {
  background-color: #f8f9fa;
  padding: 1rem;
  margin-bottom: 1rem;
  border-radius: 4px;
}

/* Cookie Toggle Layout
   This arranges the cookie options in a clear format with the
   toggle switch aligned to the right of each option. */
.cookie-toggle {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}

/* Save Button Style
   Makes the save button stand out and look clickable.
   Uses blue to match the website's color scheme. */
.button {
  background-color: #007bff;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 4px;
  cursor: pointer;
}

/* Button Hover Effect
   Makes the button slightly darker when users hover over it,
   indicating it can be clicked. */
.button:hover {
  background-color: #0056b3;
}

/* Text Styles
   These control how headings and text look throughout the website,
   ensuring consistent spacing and readability. */
h1 {
  margin-bottom: 1.5rem;
}

h2 {
  margin-top: 2rem;
}

/* Additional Information Section
   Styles for the explanatory text sections that appear at the
   bottom of pages, providing more details to users. */
.additional-info {
  margin-top: 2rem;
}